.container_user {
    background-image: url('./../../img/Group\ 1.png');
    background-size: 1480px 160px;
    background-position: center top;
    background-repeat: no-repeat;
    padding: 0 0 60px;
  }
  
  .user_row {
    display: flex;
    justify-content: space-between; 
    padding: 0 0 100px;
  }
  
  .user_logo img {
    width: 108px;
    height: 108px;
    margin-left: 36px;
    margin-top: 14px;
  }

  .user_row h1 {
    color: white;
    font-size: 53px;
    font-family: Inter;
    font-weight: 400;
    margin-right: 10px;
    margin-top: 26px;
  }

  .user_row button {
    color: white;
    margin-right: 177px;
    font-size: 20px;
  }

  .main {
    padding: 0 130px 0;
  }
  .bord {
    display: flex;
    justify-content: space-between; 
    border: 3px solid rgb(0, 80, 150);
    border-radius: 9px;
    padding: 50px 0 50px;
  }

  .user_nav {
    display: flex;
    margin-left: 100px;
    font-size: 20px;
  }

  .main h1 {
    font-size: 27px;
    margin-right: 100px;
  }

  .main button {
    width: 350px;
    height: 100px;
    font-size: 25px;
    background-color: #bebebe; 
    border: 1px solid black; 
    border-radius: 25px;
    color: black; 
    padding: 10px 20px; 
    transition: background-color 0.3s ease;
  }

  .main button:hover {
    background-color: #d2d2d2; /* Светлее цвет при наведении */
  }
  

  
  